.App-contianer {
  width: 1536px;
  height: 729.1px;
}

.top {
  max-width: 1536px;
  height: 162px;
  padding: 20px 200px 20px 200px;
}

.bottom-container ul .arrow {
  background-color: rgb(165, 162, 162, 0.4);
  cursor: pointer;
  border-radius: 10px;
  opacity: 0;
  transition: 0.5s ease;
}

.bottom-container ul li {
  cursor: pointer;
}

.bottom-container ul:hover .arrow {
  opacity: 1;
}
